<template>
  <div class="add-dialog">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="商品">
        <el-select v-model="form.spuId" filterable placeholder="请选择">
          <el-option
            v-for="item in spus"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="售价">
      <el-input-number v-model="form.salePrice"></el-input-number>
      </el-form-item>
      <el-form-item label="顾客信息">
      <el-input v-model="form.customer" placeholder="顾客信息"></el-input>
      </el-form-item>
    </el-form>
    <el-button @click="add">保存</el-button>
  </div>
</template>

<script>
export default {
  name: 'AddDialog',
  props: {
    spus: Array
  },
  data () {
    return {
      spu: {},
      form: {
        spuId: '',
        salePrice: 0,
        customer: ''
      }
    }
  },
  watch: {
    'form.spuId' () {
      this.spu = this.spus.find(item => item.id === this.form.spuId) || {}
      this.form.salePrice = Number(this.spu.price)
    }
  },
  created () {
  },
  methods: {
    add () {
      this.$emit('add', {
        ...this.form,
        ...this.spu,
        date: new Date().toLocaleString()
      })
    }
  }
}
</script>

