<template>
  <div class="daily-account">
    <el-button @click="dialogVisible = true">新增</el-button>
    <el-table :data="dataList">
      <el-table-column
        v-for="column in columnList"
        :key="column.field"
        :prop="column.field"
        :label="column.name">
      </el-table-column>
      <el-table-column
        label="操作"
      >
        <template #default="{row}">
          <input :ref="'copy'+row.date" @copy="copy($event,row)" value="test" style="position: absolute;z-index: -1;background: #fff;top:-99999px;left:-99999px;outline:none;border:none;" />
          <el-button @click="copyClick(row.date)">复制</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      width="100%"
      :visible.sync="dialogVisible"
    >
    <AddForm :spus="spus" @add="add" />
    </el-dialog>
  </div>
</template>

<script>
import AddForm from './AddForm.vue'
import spu from './spu'
export default {
  name: 'DailyAccount',
  components: { AddForm },
  data () {
    return {
      dialogVisible: false,
      spus: [],
      columnList: [
        {
          field: 'id',
          name: 'id',
          fixed: 'left'
        },
        {
          field: 'name',
          name: '商品名'
        },
        {
          field: 'price',
          name: '标价'
        },
        {
          field: 'salePrice',
          name: '售价'
        },
        {
          field: 'customer',
          name: '顾客信息'
        },
        {
          field: 'package',
          name: '包装'
        },
        {
          field: 'packagePrice',
          name: '包装价格'
        },
        {
          field: 'date',
          name: '保存日期'
        }
      ],
      dataList: []
    }
  },
  created () {
    const str = localStorage.getItem('dataList')
    const arr = str ? JSON.parse(str) : []
    this.dataList = arr
    this.spus = spu.map(item => {
      return {
        id: item[0],
        name: item[1],
        price: item[4]
      }
    })
  },
  methods: {
    add (item) {
      this.dataList.push(item)
      this.dialogVisible = false
      localStorage.setItem('dataList', JSON.stringify(this.dataList))
    },
    copyClick (tag) {
      this.$refs['copy' + tag].select()
      alert(document.execCommand)
      document.execCommand('copy')
    },
    copy (e, row) {
      alert(123)
      e.preventDefault()
      e.clipboardData.setData('text', JSON.stringify(row))
    }
  }
}
</script>

<style lang='scss' scoped>

</style>
